/**
 * Premium Visual Framework
 * Apple/Tesla-inspired minimalistic design with sleek aesthetics
 */

import React, { ReactNode } from 'react'

interface PremiumFrameworkProps {
  title?: string
  subtitle?: string
  children: ReactNode
  variant?: 'default' | 'minimal' | 'elevated'
  showHeader?: boolean
}

export const PremiumFramework: React.FC<PremiumFrameworkProps> = ({
  title,
  subtitle,
  children,
  variant = 'default',
  showHeader = true
}) => {
  const getVariantStyles = () => {
    switch (variant) {
      case 'minimal':
        return 'bg-white/50 backdrop-blur-sm border border-white/20'
      case 'elevated':
        return 'bg-white shadow-2xl border border-gray-100'
      default:
        return 'bg-white shadow-lg border border-gray-50'
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-gray-50 to-zinc-50">
      <div className="relative">
        {showHeader && (
          <header className="px-8 py-12 text-center">
            <div className="max-w-4xl mx-auto">
              {title && (
                <h1 className="text-5xl font-thin text-slate-900 tracking-tight mb-4">
                  {title}
                </h1>
              )}
              {subtitle && (
                <p className="text-lg text-slate-600 font-light leading-relaxed">
                  {subtitle}
                </p>
              )}
              {/* Elegant accent line */}
              <div className="mt-8 flex justify-center">
                <div className="w-24 h-px bg-gradient-to-r from-transparent via-slate-400 to-transparent"></div>
              </div>
            </div>
          </header>
        )}

        <main className="px-8 pb-16">
          <div className={`max-w-4xl mx-auto rounded-3xl ${getVariantStyles()} overflow-hidden`}>
            <div className="p-8 md:p-12">
              {children}
            </div>
          </div>
        </main>
      </div>
    </div>
  )
}

// Premium Button Component
export const PremiumButton: React.FC<{
  children: ReactNode
  onClick?: () => void
  variant?: 'primary' | 'secondary' | 'ghost'
  size?: 'sm' | 'md' | 'lg'
  disabled?: boolean
}> = ({ children, onClick, variant = 'primary', size = 'md', disabled = false }) => {
  const baseStyles = "font-medium rounded-full transition-all duration-300 focus:outline-none focus:ring-2 focus:ring-offset-2"

  const variants = {
    primary: "bg-slate-900 text-white hover:bg-slate-800 focus:ring-slate-500 shadow-lg hover:shadow-xl",
    secondary: "bg-white text-slate-900 border border-slate-200 hover:border-slate-300 hover:bg-slate-50 focus:ring-slate-500",
    ghost: "text-slate-600 hover:text-slate-900 hover:bg-slate-100 focus:ring-slate-500"
  }

  const sizes = {
    sm: "px-4 py-2 text-sm",
    md: "px-6 py-3 text-base",
    lg: "px-8 py-4 text-lg"
  }

  return (
    <button
      onClick={onClick}
      disabled={disabled}
      className={`${baseStyles} ${variants[variant]} ${sizes[size]} ${disabled ? 'opacity-50 cursor-not-allowed' : ''}`}
    >
      {children}
    </button>
  )
}

// Premium Input Component
export const PremiumInput: React.FC<{
  placeholder?: string
  value?: string
  onChange?: (value: string) => void
  multiline?: boolean
  rows?: number
}> = ({ placeholder, value, onChange, multiline = false, rows = 3 }) => {
  const baseStyles = "w-full bg-slate-50 border border-slate-200 rounded-2xl px-6 py-4 text-slate-900 placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-slate-500 focus:border-transparent transition-all duration-300"

  if (multiline) {
    return (
      <textarea
        placeholder={placeholder}
        value={value}
        onChange={(e) => onChange?.(e.target.value)}
        rows={rows}
        className={`${baseStyles} resize-none`}
      />
    )
  }

  return (
    <input
      type="text"
      placeholder={placeholder}
      value={value}
      onChange={(e) => onChange?.(e.target.value)}
      className={baseStyles}
    />
  )
}

// Premium Loading Component
export const PremiumLoading: React.FC<{
  message?: string
}> = ({ message = "Creating..." }) => {
  return (
    <div className="flex flex-col items-center justify-center py-16 space-y-6">
      <div className="relative">
        <div className="w-12 h-12 border-2 border-slate-200 rounded-full"></div>
        <div className="absolute inset-0 w-12 h-12 border-2 border-slate-900 rounded-full border-t-transparent animate-spin"></div>
      </div>
      <div className="text-center space-y-2">
        <p className="text-slate-900 font-medium">{message}</p>
        <p className="text-slate-500 text-sm">Crafting your perfect application</p>
      </div>
    </div>
  )
}

// Premium Error Component
export const PremiumError: React.FC<{
  title?: string
  message: string
  onRetry?: () => void
}> = ({ title = "Something went wrong", message, onRetry }) => {
  return (
    <div className="text-center py-16 space-y-6">
      <div className="w-16 h-16 mx-auto bg-red-50 rounded-full flex items-center justify-center">
        <svg className="w-8 h-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L4.082 16.5c-.77.833.192 2.5 1.732 2.5z" />
        </svg>
      </div>
      <div className="space-y-2">
        <h3 className="text-xl font-semibold text-slate-900">{title}</h3>
        <p className="text-slate-600 max-w-md mx-auto">{message}</p>
      </div>
      {onRetry && (
        <PremiumButton onClick={onRetry} variant="secondary">
          Try Again
        </PremiumButton>
      )}
    </div>
  )
}

// Premium Card Component
export const PremiumCard: React.FC<{
  title?: string
  children: ReactNode
  variant?: 'default' | 'elevated'
}> = ({ title, children, variant = 'default' }) => {
  const cardStyles = variant === 'elevated'
    ? 'bg-white shadow-xl border border-slate-100'
    : 'bg-slate-50/50 border border-slate-200/50'

  return (
    <div className={`rounded-2xl ${cardStyles} overflow-hidden`}>
      {title && (
        <div className="px-6 py-4 border-b border-slate-100 bg-slate-50/30">
          <h3 className="text-lg font-semibold text-slate-900">{title}</h3>
        </div>
      )}
      <div className="p-6">
        {children}
      </div>
    </div>
  )
}

export default PremiumFramework
