import React, { useState } from 'react'
import { motion } from 'framer-motion'
import {
  Grid3X3,
  Search,

  Star,
  Clock,

  Share2,
  Edit3,
  Trash2,
  Eye,
  Smartphone,
  Globe
} from 'lucide-react'

// Mock data for generated apps
const mockApps = [
  {
    id: '1',
    name: 'Habit Tracker Pro',
    description: 'Track daily habits with streaks and analytics',
    thumbnail: 'üìä',
    features: ['Progress Charts', 'Streak Counter', 'Reminders', 'Statistics'],
    createdAt: new Date(Date.now() - 86400000),
    status: 'published',
    platform: 'pwa',
    rating: 4.8,
    downloads: 1247
  },
  {
    id: '2',
    name: 'Recipe Manager',
    description: 'Organize recipes with shopping lists and timers',
    thumbnail: 'üë®‚Äçüç≥',
    features: ['Ingredient Lists', 'Cooking Timer', 'Shopping List', 'Meal Planning'],
    createdAt: new Date(Date.now() - 172800000),
    status: 'published',
    platform: 'web',
    rating: 4.6,
    downloads: 892
  },
  {
    id: '3',
    name: 'Expense Tracker',
    description: 'Monitor spending with budgets and analytics',
    thumbnail: 'üí∞',
    features: ['Budget Categories', 'Spending Analytics', 'Receipt Scanning', 'Reports'],
    createdAt: new Date(Date.now() - 259200000),
    status: 'draft',
    platform: 'pwa',
    rating: null,
    downloads: 0
  },
  {
    id: '4',
    name: 'Workout Planner',
    description: 'Create custom workout routines and track progress',
    thumbnail: 'üí™',
    features: ['Exercise Library', 'Custom Routines', 'Progress Tracking', 'Rest Timer'],
    createdAt: new Date(Date.now() - 345600000),
    status: 'published',
    platform: 'pwa',
    rating: 4.9,
    downloads: 2156
  }
]

const AppGallery: React.FC = () => {
  const [apps, setApps] = useState(mockApps)
  const [searchQuery, setSearchQuery] = useState('')
  const [filterStatus, setFilterStatus] = useState<'all' | 'published' | 'draft'>('all')
  const [filterPlatform, setFilterPlatform] = useState<'all' | 'pwa' | 'web'>('all')
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid')

  const filteredApps = apps.filter(app => {
    const matchesSearch = app.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                         app.description.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesStatus = filterStatus === 'all' || app.status === filterStatus
    const matchesPlatform = filterPlatform === 'all' || app.platform === filterPlatform

    return matchesSearch && matchesStatus && matchesPlatform
  })

  const handleDeleteApp = (appId: string) => {
    setApps(prev => prev.filter(app => app.id !== appId))
  }

  const getStatusColor = (status: string) => {
    return status === 'published' ? 'bg-green-100 text-green-800' : 'bg-yellow-100 text-yellow-800'
  }

  const getPlatformIcon = (platform: string) => {
    return platform === 'pwa' ? <Smartphone className="w-4 h-4" /> : <Globe className="w-4 h-4" />
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">My Apps</h1>
          <p className="text-gray-600">Manage and share your generated applications</p>
        </div>

        <div className="flex items-center space-x-3">
          <button
            onClick={() => setViewMode(viewMode === 'grid' ? 'list' : 'grid')}
            className="p-2 bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors"
          >
            <Grid3X3 className="w-5 h-5" />
          </button>
        </div>
      </div>

      {/* Search and Filters */}
      <div className="card">
        <div className="flex flex-col md:flex-row gap-4">
          {/* Search */}
          <div className="flex-1 relative">
            <Search className="w-5 h-5 absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
            <input
              type="text"
              placeholder="Search your apps..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="input-primary pl-10"
            />
          </div>

          {/* Filters */}
          <div className="flex gap-2">
            <select
              value={filterStatus}
              onChange={(e) => setFilterStatus(e.target.value as any)}
              className="input-primary"
            >
              <option value="all">All Status</option>
              <option value="published">Published</option>
              <option value="draft">Draft</option>
            </select>

            <select
              value={filterPlatform}
              onChange={(e) => setFilterPlatform(e.target.value as any)}
              className="input-primary"
            >
              <option value="all">All Platforms</option>
              <option value="pwa">PWA</option>
              <option value="web">Web</option>
            </select>
          </div>
        </div>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <div className="card text-center">
          <div className="text-2xl font-bold text-blue-600">{apps.length}</div>
          <div className="text-sm text-gray-600">Total Apps</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bold text-green-600">
            {apps.filter(app => app.status === 'published').length}
          </div>
          <div className="text-sm text-gray-600">Published</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bold text-purple-600">
            {apps.reduce((sum, app) => sum + app.downloads, 0)}
          </div>
          <div className="text-sm text-gray-600">Downloads</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bold text-yellow-600">
            {apps.filter(app => app.rating).reduce((sum, app) => sum + (app.rating || 0), 0) /
             apps.filter(app => app.rating).length || 0}
          </div>
          <div className="text-sm text-gray-600">Avg Rating</div>
        </div>
      </div>

      {/* Apps Grid/List */}
      <div className={`grid gap-6 ${viewMode === 'grid' ? 'md:grid-cols-2 lg:grid-cols-3' : 'grid-cols-1'}`}>
        {filteredApps.map((app, index) => (
          <motion.div
            key={app.id}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
            className="card group hover:shadow-xl transition-all duration-200"
          >
            {viewMode === 'grid' ? (
              // Grid View
              <div className="space-y-4">
                {/* Thumbnail */}
                <div className="aspect-video bg-gradient-to-br from-blue-100 to-purple-100 rounded-lg flex items-center justify-center text-4xl">
                  {app.thumbnail}
                </div>

                {/* Content */}
                <div className="space-y-3">
                  <div className="flex items-start justify-between">
                    <div>
                      <h3 className="font-semibold text-gray-900 group-hover:text-blue-600 transition-colors">
                        {app.name}
                      </h3>
                      <p className="text-sm text-gray-600 line-clamp-2">{app.description}</p>
                    </div>
                    <span className={`px-2 py-1 text-xs rounded-full ${getStatusColor(app.status)}`}>
                      {app.status}
                    </span>
                  </div>

                  {/* Features */}
                  <div className="flex flex-wrap gap-1">
                    {app.features.slice(0, 3).map((feature, idx) => (
                      <span key={idx} className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded">
                        {feature}
                      </span>
                    ))}
                    {app.features.length > 3 && (
                      <span className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded">
                        +{app.features.length - 3}
                      </span>
                    )}
                  </div>

                  {/* Stats */}
                  <div className="flex items-center justify-between text-sm text-gray-500">
                    <div className="flex items-center space-x-4">
                      <div className="flex items-center space-x-1">
                        {getPlatformIcon(app.platform)}
                        <span className="capitalize">{app.platform}</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <Clock className="w-4 h-4" />
                        <span>{new Date(app.createdAt).toLocaleDateString()}</span>
                      </div>
                    </div>
                    {app.rating && (
                      <div className="flex items-center space-x-1">
                        <Star className="w-4 h-4 text-yellow-500 fill-current" />
                        <span>{app.rating}</span>
                      </div>
                    )}
                  </div>
                </div>

                {/* Actions */}
                <div className="flex space-x-2 pt-2 border-t border-gray-100">
                  <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    className="flex-1 btn-secondary text-xs py-2"
                  >
                    <Eye className="w-3 h-3 mr-1" />
                    View
                  </motion.button>
                  <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    className="flex-1 btn-secondary text-xs py-2"
                  >
                    <Edit3 className="w-3 h-3 mr-1" />
                    Edit
                  </motion.button>
                  <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    className="flex-1 btn-secondary text-xs py-2"
                  >
                    <Share2 className="w-3 h-3 mr-1" />
                    Share
                  </motion.button>
                </div>
              </div>
            ) : (
              // List View
              <div className="flex items-center space-x-4">
                {/* Thumbnail */}
                <div className="w-16 h-16 bg-gradient-to-br from-blue-100 to-purple-100 rounded-lg flex items-center justify-center text-2xl flex-shrink-0">
                  {app.thumbnail}
                </div>

                {/* Content */}
                <div className="flex-1 min-w-0">
                  <div className="flex items-start justify-between mb-2">
                    <div className="min-w-0 flex-1">
                      <h3 className="font-semibold text-gray-900 truncate group-hover:text-blue-600 transition-colors">
                        {app.name}
                      </h3>
                      <p className="text-sm text-gray-600 line-clamp-1">{app.description}</p>
                    </div>
                    <span className={`px-2 py-1 text-xs rounded-full ml-2 ${getStatusColor(app.status)}`}>
                      {app.status}
                    </span>
                  </div>

                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-4 text-xs text-gray-500">
                      <div className="flex items-center space-x-1">
                        {getPlatformIcon(app.platform)}
                        <span className="capitalize">{app.platform}</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <Clock className="w-3 h-3" />
                        <span>{new Date(app.createdAt).toLocaleDateString()}</span>
                      </div>
                      {app.downloads > 0 && (
                        <span>{app.downloads} downloads</span>
                      )}
                    </div>

                    <div className="flex space-x-1">
                      <motion.button
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        className="p-1 text-gray-400 hover:text-blue-600 transition-colors"
                      >
                        <Eye className="w-4 h-4" />
                      </motion.button>
                      <motion.button
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        className="p-1 text-gray-400 hover:text-blue-600 transition-colors"
                      >
                        <Edit3 className="w-4 h-4" />
                      </motion.button>
                      <motion.button
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        onClick={() => handleDeleteApp(app.id)}
                        className="p-1 text-gray-400 hover:text-red-600 transition-colors"
                      >
                        <Trash2 className="w-4 h-4" />
                      </motion.button>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </motion.div>
        ))}
      </div>

      {/* Empty State */}
      {filteredApps.length === 0 && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="text-center py-12"
        >
          <div className="w-24 h-24 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <Grid3X3 className="w-12 h-12 text-gray-400" />
          </div>
          <h3 className="text-lg font-medium text-gray-900 mb-2">No apps found</h3>
          <p className="text-gray-600 mb-6">
            {searchQuery || filterStatus !== 'all' || filterPlatform !== 'all'
              ? 'Try adjusting your search or filters'
              : 'Create your first app to get started'}
          </p>
          <a href="/create" className="btn-primary inline-flex items-center space-x-2">
            <span>Create Your First App</span>
          </a>
        </motion.div>
      )}
    </div>
  )
}

export default AppGallery
