import React from 'react'
import AppRenderer from '../components/AppRenderer'
import { PremiumFramework, PremiumInput, PremiumButton, PremiumLoading, PremiumError, PremiumCard } from '../components/PremiumFramework'
import { useAppGenerator } from '../hooks/use-app-generator'

const AppCreator: React.FC = () => {
  const {
    description,
    setDescription,
    isGenerating,
    generatedApp,
    error,
    generateApp
  } = useAppGenerator()

  return (
    <PremiumFramework
      title="AI App Builder"
      subtitle="Craft extraordinary applications with artificial intelligence"
    >
      <div className="space-y-8">
        {/* Input Section */}
        <PremiumCard>
          <div className="space-y-6">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-2">
                Describe Your App
              </label>
              <PremiumInput
                placeholder="Tell us what you want to build..."
                value={description}
                onChange={setDescription}
                multiline={true}
                rows={4}
              />
            </div>

            <div className="flex justify-center">
              <PremiumButton
                onClick={generateApp}
                disabled={!description.trim() || isGenerating}
              >
                {isGenerating ? (
                  <>
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2"></div>
                    Creating Your App
                  </>
                ) : (
                  'Generate App'
                )}
              </PremiumButton>
            </div>
          </div>
        </PremiumCard>

        {/* Loading State */}
        {isGenerating && (
          <PremiumCard>
            <PremiumLoading message="AI is crafting your application..." />
          </PremiumCard>
        )}

        {/* Error State */}
        {error && !isGenerating && (
          <PremiumError
            message={error}
            onRetry={generateApp}
          />
        )}

        {/* Generated App */}
        {generatedApp && !isGenerating && (
          <PremiumCard title={generatedApp.name}>
            <div className="mb-4">
              <p className="text-slate-600 text-sm leading-relaxed">
                {generatedApp.description}
              </p>
            </div>
            <div className="border-t border-slate-100 pt-6">
              <AppRenderer app={generatedApp} />
            </div>
          </PremiumCard>
        )}
      </div>
    </PremiumFramework>
  )
}

export default AppCreator