/**
 * Custom Hook for App Generation
 * Manages state and API calls for app generation
 */

import { useState } from 'react'

export interface GeneratedApp {
  id: string
  name: string
  description: string
  type: string
  code: string
  complexity: string
  features: string[]
  securityStatus: 'secure' | 'warnings'
  createdAt: Date
  components: Record<string, string>
  styles: string
  config: Record<string, any>
}

export interface UseAppGeneratorReturn {
  description: string
  setDescription: (desc: string) => void
  isGenerating: boolean
  generatedApp: GeneratedApp | null
  error: string | null
  generateApp: () => Promise<void>
  clearError: () => void
  reset: () => void
}

/**
 * Custom hook for managing app generation state and logic
 */
export function useAppGenerator(): UseAppGeneratorReturn {
  const [description, setDescription] = useState('')
  const [isGenerating, setIsGenerating] = useState(false)
  const [generatedApp, setGeneratedApp] = useState<GeneratedApp | null>(null)
  const [error, setError] = useState<string | null>(null)

  const handleGenerateApp = async () => {
    if (!description.trim()) {
      setError('Please describe the app you want to create')
      return
    }

    setIsGenerating(true)
    setError(null)
    setGeneratedApp(null)

    try {
      const response = await fetch('/api/grok', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          prompt: description.trim(),
          model: 'grok-code-fast-1'
        })
      })

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({ error: 'Network error' }))
        setError(errorData.error || `HTTP ${response.status}`)
        return
      }

      const data = await response.json()
      const generatedCode = data.code

      if (!generatedCode) {
        setError('No code was generated')
        return
      }

      // Create the generated app object
      const app: GeneratedApp = {
        id: `app_${Date.now()}`,
        name: description.split(' ').slice(0, 3).join(' ') || 'App',
        description: description,
        type: 'react',
        code: generatedCode,
        complexity: 'medium',
        features: ['interactive', 'responsive'],
        securityStatus: 'secure',
        createdAt: new Date(),
        components: {},
        styles: '',
        config: {}
      }

      setGeneratedApp(app)

    } catch (err) {
      console.error('App generation error:', err)
      setError(err instanceof Error ? err.message : 'An unexpected error occurred')
    } finally {
      setIsGenerating(false)
    }
  }

  const clearError = () => setError(null)

  const reset = () => {
    setDescription('')
    setIsGenerating(false)
    setGeneratedApp(null)
    setError(null)
  }

  return {
    description,
    setDescription,
    isGenerating,
    generatedApp,
    error,
    generateApp: handleGenerateApp,
    clearError,
    reset
  }
}
