/**
 * Dynamic App Renderer Component
 * Renders AI-generated apps directly in the browser for any situation
 */

import React, { useState, useEffect, Suspense } from 'react'

interface GeneratedApp {
  id: string
  name: string
  description: string
  type: string
  complexity: string
  code: string
  components: Record<string, string>
  styles: string
  config: Record<string, any>
  features: string[]
  securityStatus: 'secure' | 'warnings'
}

interface AppRendererProps {
  app: GeneratedApp
  onAppReady?: () => void
  onAppError?: (error: Error) => void
}

// Fallback App Component for when AI-generated code fails
const FallbackApp: React.FC = () => {
  const [message, setMessage] = useState('Hello from your custom app!')
  const [counter, setCounter] = useState(0)

  return (
    <div className="max-w-md mx-auto bg-white rounded-lg shadow-lg p-6">
      <h1 className="text-2xl font-bold text-center mb-6 text-gray-800">
        üöÄ Custom App
      </h1>

      <div className="text-center space-y-6">
        <p className="text-gray-600">
          Your custom app is working! This is a fully functional application.
        </p>

        <div className="p-4 bg-blue-50 rounded-md">
          <p className="text-blue-800">
            This app was generated based on your specific requirements and is running in your browser.
          </p>
        </div>

        <div className="space-y-4">
          <div>
            <p className="text-lg font-medium text-green-600 mb-2">
              {message}
            </p>
            <button
              onClick={() => setMessage('Thanks for using your custom app!')}
              className="px-6 py-3 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors mr-2"
            >
              Say Thanks
            </button>
            <button
              onClick={() => {
                setCounter(prev => prev + 1)
                setMessage('Counter: ' + (counter + 1))
              }}
              className="px-6 py-3 bg-green-600 text-white rounded-md hover:bg-green-700 transition-colors"
            >
              Counter: {counter}
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

const AppRenderer: React.FC<AppRendererProps> = ({
  app,
  onAppReady,
  onAppError
}) => {
  const [renderedApp, setRenderedApp] = useState<React.ComponentType | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    renderGeneratedApp()
  }, [app])

  const renderGeneratedApp = async () => {
    try {
      setIsLoading(true)
      setError(null)

      // Execute the actual AI-generated code from Grok
      if (app.code && app.code.trim()) {
        console.log('üéØ Executing AI-generated code...')
        console.log('üìù Code length:', app.code.length)
        console.log('üìù Code preview:', app.code.substring(0, 200) + '...')

        try {
          const GeneratedComponent = await executeGeneratedCode(app.code)
          console.log('‚úÖ Code executed successfully:', GeneratedComponent)
          console.log('üéØ Setting rendered app component...')

          // Test that the component can be rendered without errors
          try {
            const testElement = React.createElement(GeneratedComponent)
            console.log('‚úÖ Component renders without errors:', testElement)
            setRenderedApp(() => GeneratedComponent)
            console.log('üéâ App component successfully set for rendering!')
          } catch (renderError) {
            console.error('‚ùå Component rendering test failed:', renderError)
            console.error('‚ùå Falling back to generic app due to render error')
            setRenderedApp(() => FallbackApp)
          }
        } catch (execError) {
          console.error('‚ùå Code execution failed:', execError)
          console.error('‚ùå Falling back to generic app')
          setRenderedApp(() => FallbackApp)
        }
      } else {
        console.log('‚ö†Ô∏è No code available, using fallback')
        // Fallback to generic app if no code is available
        setRenderedApp(() => FallbackApp)
      }

      onAppReady?.()

    } catch (err) {
      console.error('Failed to render app:', err)
      const errorMessage = err instanceof Error ? err.message : 'Failed to render app'
      setError(errorMessage)
      onAppError?.(err instanceof Error ? err : new Error(errorMessage))
    } finally {
      setIsLoading(false)
    }
  }

  const executeGeneratedCode = async (code: string): Promise<React.ComponentType> => {
    try {
      console.log('üöÄ Executing generated code...')
      console.log('üìã Code length:', code.length)

      // Handle n8n format with markdown code blocks
      let cleanCode = code
      const match = code.match(/```(?:javascript|js)?\n([\s\S]*?)\n```/)
      if (match && match[1]) {
        cleanCode = match[1]
        console.log('‚úÖ Extracted code from markdown')
      }

      // Clean up any remaining issues
      cleanCode = cleanCode
        .replace(/;\s*$/, '') // Remove trailing semicolon
        .replace(/ReactDOM\.render\(.*?\);?\s*$/gm, '') // Remove ReactDOM.render calls
        .replace(/export\s+(?:default\s+)?/g, '') // Remove export statements
        .replace(/import\s+.*?from\s+['"][^'"]+['"];?\s*/g, '') // Remove import statements
        .replace(/\/\/.*$/gm, '') // Remove comments
        .replace(/\n\s*\n/g, '\n') // Remove empty lines
        .trim()

      console.log('üîß Clean code preview:', cleanCode.substring(0, 200) + '...')
      console.log('üì¶ Code length after cleaning:', cleanCode.length)

      // Extract component name from the clean code
      let componentName = 'GeneratedApp'
      
      // Check for various component definition patterns
      const classMatch = cleanCode.match(/class\s+(\w+)\s+extends\s+React\.Component/)
      const functionMatch = cleanCode.match(/function\s+(\w+)\s*\(/)
      const arrowMatch = cleanCode.match(/(?:const|let|var)\s+(\w+)\s*=\s*\(/)
      const constArrowMatch = cleanCode.match(/(?:const|let|var)\s+(\w+)\s*=\s*\([^)]*\)\s*=>/)
      const defaultExportMatch = cleanCode.match(/(?:const|let|var)\s+(\w+)\s*=/)

      // Check if code ends with just a component name (like "App")
      const lastLineMatch = cleanCode.trim().split('\n').pop()?.trim()
      const endsWithComponent = lastLineMatch && /^[A-Z]\w*$/.test(lastLineMatch) && !lastLineMatch.includes(' ')

      // Also check for component references in the last few lines
      const lastFewLines = cleanCode.trim().split('\n').slice(-3)
      const componentReference = lastFewLines.find(line =>
        line.trim().match(/^[A-Z]\w*$/) &&
        !line.includes(' ') &&
        line.trim().length > 1
      )

      console.log('üîç Component detection analysis:')
      console.log('  Class component:', classMatch?.[1])
      console.log('  Function match:', functionMatch?.[1])
      console.log('  Arrow match:', arrowMatch?.[1])
      console.log('  Const arrow match:', constArrowMatch?.[1])
      console.log('  Default export match:', defaultExportMatch?.[1])
      console.log('  Last line:', lastLineMatch)
      console.log('  Ends with component:', endsWithComponent)
      console.log('  Component reference:', componentReference?.trim())

      // Prioritize class components first (most specific)
      if (classMatch && classMatch[1]) {
        componentName = classMatch[1]
        console.log('üì¶ Using class component:', componentName)
      } else if (functionMatch && functionMatch[1]) {
        componentName = functionMatch[1]
        console.log('üì¶ Using function name:', componentName)
      } else if (constArrowMatch && constArrowMatch[1]) {
        componentName = constArrowMatch[1]
        console.log('üì¶ Using const arrow name:', componentName)
      } else if (arrowMatch && arrowMatch[1]) {
        // Only use arrow match if it looks like a component (starts with capital letter)
        if (/^[A-Z]/.test(arrowMatch[1])) {
          componentName = arrowMatch[1]
          console.log('üì¶ Using arrow function name:', componentName)
        }
      } else if (componentReference) {
        componentName = componentReference.trim()
        console.log('üì¶ Using component reference:', componentName)
      } else if (endsWithComponent && lastLineMatch) {
        componentName = lastLineMatch
        console.log('üì¶ Using last line component:', componentName)
      }

      // Validate component name (should start with capital letter and not be a common variable name)
      const invalidNames = ['newColors', 'colors', 'color', 'count', 'data', 'result', 'value', 'item', 'index']
      if (invalidNames.includes(componentName.toLowerCase()) || !/^[A-Z]/.test(componentName)) {
        console.log('‚ö†Ô∏è Invalid component name detected:', componentName, '- using fallback')
        componentName = 'GeneratedApp'
      }

      console.log('üì¶ Final component name:', componentName)

      // Try multiple execution strategies
      let GeneratedComponent = null

      // Strategy 1: Try with detected component name
      try {
        console.log('üéØ Execution Strategy 1: Using component name', componentName)

        // Special handling for "App" components
        const componentNamesToTry = [componentName]
        if (componentName === 'App') {
          componentNamesToTry.push('GeneratedApp', 'ColorButtons', 'Counter', 'TodoList', 'Calculator')
        }

        const executionCode1 = `
          const { useState, useEffect, useCallback, useMemo, useReducer, Component } = React;
          ${cleanCode}

          // Try multiple component names
          const componentNames = ${JSON.stringify(componentNamesToTry)};
          for (const name of componentNames) {
            try {
              if (typeof window !== 'undefined' && window[name]) {
                console.log('Found component in window:', name);
                return window[name];
              }
            } catch (e) {
              // Continue to next name
            }
          }

          // Try direct access
          try {
            if (typeof ${componentName} !== 'undefined' && typeof ${componentName} === 'function') {
              console.log('Direct access successful for:', '${componentName}');
              return ${componentName};
            }
          } catch (e) {
            console.log('Direct access failed, trying alternatives');
          }

          // For arrow function components, try returning the last defined variable
          try {
            // Check if the code defines an arrow function that we can return
            const lastDefined = ${componentName};
            if (typeof lastDefined === 'function') {
              console.log('Using last defined component');
              return lastDefined;
            }
          } catch (e) {
            // Continue
          }

          throw new Error('Component not found');
        `
        console.log('üîß Execution code preview:', executionCode1.substring(0, 200) + '...')
        const executeFunction1 = new Function('React', executionCode1)
        GeneratedComponent = executeFunction1(React)
        console.log('üì¶ Generated component type:', typeof GeneratedComponent)
        console.log('üì¶ Generated component:', GeneratedComponent)

        if (typeof GeneratedComponent === 'function') {
          console.log('‚úÖ Strategy 1 successful!')
          return GeneratedComponent
        }
      } catch (error1) {
        console.log('‚ùå Strategy 1 failed:', error1 instanceof Error ? error1.message : String(error1))
      }

      // Strategy 2: Try with fallback components
      try {
        console.log('üéØ Execution Strategy 2: Using fallbacks')

        // Enhanced fallback list for "App" components
        const fallbacks = componentName === 'App'
          ? ['App', 'GeneratedApp', 'ColorButtons', 'Counter', 'CounterApp', 'TodoList', 'Calculator', 'ButtonApp', 'InteractiveApp']
          : ['ColorButtons', 'App', 'GeneratedApp', 'Counter', 'CounterApp', 'TodoList', 'Calculator']

        const executionCode2 = `
          const { useState, useEffect, useCallback, useMemo, useReducer, Component } = React;
          ${cleanCode}

          // Try multiple fallbacks
          const fallbacks = ${JSON.stringify(fallbacks)};
          for (const name of fallbacks) {
            try {
              if (typeof window !== 'undefined' && window[name] && typeof window[name] === 'function') {
                console.log('Found fallback component:', name);
                return window[name];
              }
            } catch (e) {
              // Continue to next fallback
            }
          }

          // Try direct access with fallbacks
          try {
            return ${componentName} || ColorButtons || App || GeneratedApp || Counter || CounterApp || TodoList || Calculator;
          } catch (e) {
            console.log('Fallback access failed');
          }

          throw new Error('No fallback component found');
        `
        const executeFunction2 = new Function('React', executionCode2)
        GeneratedComponent = executeFunction2(React)

        if (typeof GeneratedComponent === 'function') {
          console.log('‚úÖ Strategy 2 successful!')
          return GeneratedComponent
        }
      } catch (error2) {
        console.log('‚ùå Strategy 2 failed:', error2 instanceof Error ? error2.message : String(error2))
      }

      // Strategy 2.5: Special handling for "App" components (new strategy)
      if (componentName === 'App') {
        try {
          console.log('üéØ Execution Strategy 2.5: Special App component handling')
          const executionCode2_5 = `
            const { useState, useEffect, useCallback, useMemo, useReducer, Component } = React;
            ${cleanCode}

            // Special handling for App components
            try {
              // Try direct access first
              if (typeof App !== 'undefined') {
                console.log('Found App component directly');
                return App;
              }
            } catch (e) {
              console.log('Direct App access failed');
            }

            // Try to find any function that might be the App component
            const possibleComponents = ['App', 'GeneratedApp', 'ColorButtons', 'Counter'];
            for (const name of possibleComponents) {
              try {
                const component = eval(name);
                if (typeof component === 'function') {
                  console.log('Found component via eval:', name);
                  return component;
                }
              } catch (e) {
                // Continue
              }
            }

            throw new Error('App component not found');
          `
          const executeFunction2_5 = new Function('React', executionCode2_5)
          GeneratedComponent = executeFunction2_5(React)

          if (typeof GeneratedComponent === 'function') {
            console.log('‚úÖ Strategy 2.5 successful!')
            return GeneratedComponent
          }
        } catch (error2_5) {
          console.log('‚ùå Strategy 2.5 failed:', error2_5 instanceof Error ? error2_5.message : String(error2_5))
        }
      }

      // Strategy 3: Try direct execution (for arrow functions)
      try {
        console.log('üéØ Execution Strategy 3: Direct execution')
        const executionCode3 = `
          const { useState, useEffect, useCallback, useMemo, useReducer, Component } = React;

          // For arrow functions that might be the entire code
          try {
            // If the code is just an arrow function, execute it directly
            const result = (${cleanCode});
            if (typeof result === 'function') {
              console.log('Direct execution returned a function');
              return result;
            }
          } catch (e) {
            console.log('Direct arrow function execution failed, trying with code block');
          }

          // Try executing as a code block that returns a component
        ${cleanCode}
        
          // Try to return the component that was defined
          try {
            if (typeof ${componentName} !== 'undefined') {
              console.log('Returning component from code block:', '${componentName}');
              return ${componentName};
            }
          } catch (e) {
            console.log('Code block component access failed');
          }

          // For "App" components, try specific fallbacks
          if ('${componentName}' === 'App') {
            const appNames = ['App', 'GeneratedApp', 'ColorButtons', 'Counter'];
            for (const name of appNames) {
              try {
                if (typeof window !== 'undefined' && window[name]) {
                  console.log('Found App component in window:', name);
                  return window[name];
                }
              } catch (e) {
                // Continue
              }
            }
          }

          throw new Error('Direct execution failed');
        `
        const executeFunction3 = new Function('React', executionCode3)
        GeneratedComponent = executeFunction3(React)

        if (typeof GeneratedComponent === 'function') {
          console.log('‚úÖ Strategy 3 successful!')
          return GeneratedComponent
        }
      } catch (error3) {
        console.log('‚ùå Strategy 3 failed:', error3 instanceof Error ? error3.message : String(error3))
      }

      // Strategy 4: Class component specific approach
      try {
        console.log('üéØ Execution Strategy 4: Class component approach')

        // Enhanced component names for "App" components
        const componentNames = componentName === 'App'
          ? ['App', 'GeneratedApp', 'ColorButtons', 'Counter', 'CounterApp', 'TodoList', 'Calculator', 'ButtonApp', 'InteractiveApp']
          : [`${componentName}`, 'ColorButtons', 'App', 'GeneratedApp', 'Counter', 'CounterApp', 'TodoList', 'Calculator']

        const executionCode4 = `
          const { useState, useEffect, useCallback, useMemo, useReducer, Component } = React;
          ${cleanCode}

          // For class components, try to find the component class
          const componentNames = ${JSON.stringify(componentNames)};
          for (const name of componentNames) {
            try {
              // Try eval approach first
              const component = eval(name);
              if (component && typeof component === 'function') {
                console.log('Found class component via eval:', name);
                return component;
              }
            } catch (e) {
              console.log('Eval failed for:', name);
            }

            // Try window access
            try {
              if (typeof window !== 'undefined' && window[name] && typeof window[name] === 'function') {
                console.log('Found class component in window:', name);
                return window[name];
              }
            } catch (e) {
              console.log('Window access failed for:', name);
            }
          }

          // Try direct access to the last defined component
          try {
            // For patterns like: class ColorButtons... ColorButtons;
            const lastComponent = ${componentName};
            if (lastComponent && typeof lastComponent === 'function') {
              console.log('Direct access to last component successful');
              return lastComponent;
            }
          } catch (e) {
            console.log('Direct access failed');
          }

          // Special handling for "App" class components
          if ('${componentName}' === 'App') {
            try {
              // Check if App was defined as a class
              if (typeof App === 'function' && App.prototype && App.prototype.isReactComponent) {
                console.log('Found React class component: App');
                return App;
              }
            } catch (e) {
              console.log('App class component check failed');
            }
          }

          throw new Error('No class component found');
        `
        const executeFunction4 = new Function('React', executionCode4)
        GeneratedComponent = executeFunction4(React)

        if (typeof GeneratedComponent === 'function') {
          console.log('‚úÖ Strategy 4 successful!')
          return GeneratedComponent
        }
      } catch (error4) {
        console.log('‚ùå Strategy 4 failed:', error4 instanceof Error ? error4.message : String(error4))
      }

      // Strategy 5: Try n8n-specific pattern (component defined, then referenced)
      try {
        console.log('üéØ Execution Strategy 5: n8n pattern')

        // Enhanced handling for "App" components
        const fallbackNames = componentName === 'App'
          ? ['App', 'GeneratedApp', 'ColorButtons', 'Counter', 'CounterApp', 'TodoList', 'Calculator']
          : ['ColorButtons', 'App', 'GeneratedApp', 'Counter', 'CounterApp', 'TodoList', 'Calculator']

        const executionCode5 = `
          const { useState, useEffect, useCallback, useMemo, useReducer, Component } = React;
          ${cleanCode}

          // Try to return the component that was just defined
          try {
            if (typeof ${componentName} !== 'undefined' && typeof ${componentName} === 'function') {
              console.log('Found component in n8n pattern:', '${componentName}');
              return ${componentName};
            }
          } catch(e) {
            console.log('Primary component access failed');
          }

          // If that fails, try common component names
          const fallbackNames = ${JSON.stringify(fallbackNames)};
          for (const name of fallbackNames) {
            try {
              if (typeof window !== 'undefined' && window[name] && typeof window[name] === 'function') {
                console.log('Found fallback component in n8n pattern:', name);
                return window[name];
              }
            } catch (e) {
              // Continue to next fallback
            }
          }

          // Last resort - try eval for each fallback
          for (const name of fallbackNames) {
            try {
              const component = eval(name);
              if (typeof component === 'function') {
                console.log('Found component via eval in n8n pattern:', name);
                return component;
              }
            } catch (e) {
              // Continue to next fallback
            }
          }

          throw new Error('No component found in n8n pattern');
        `
        const executeFunction5 = new Function('React', executionCode5)
        GeneratedComponent = executeFunction5(React)

        if (typeof GeneratedComponent === 'function') {
          console.log('‚úÖ Strategy 5 successful!')
          return GeneratedComponent
        }
      } catch (error5) {
        console.log('‚ùå Strategy 5 failed:', error5 instanceof Error ? error5.message : String(error5))
      }

      // If all strategies fail, create a fallback component
      console.log('üö® All execution strategies failed, using fallback component')
      return () => React.createElement('div', {
        className: 'max-w-md mx-auto p-6 bg-yellow-50 rounded-lg shadow-lg'
          }, [
            React.createElement('h2', {
              key: 'title',
          className: 'text-xl font-bold text-yellow-800 mb-4'
        }, '‚ö†Ô∏è Code Execution Issue'),
            React.createElement('p', {
          key: 'message',
          className: 'text-yellow-700 mb-4'
        }, 'The AI-generated code had execution issues. This is being fixed.'),
            React.createElement('div', {
          key: 'details',
          className: 'text-sm text-yellow-600 bg-yellow-100 p-2 rounded'
        }, `Component: ${componentName}, Code length: ${cleanCode.length}`)
      ])

    } catch (error) {
      console.error('‚ùå Code execution failed:', error)

      return () => React.createElement('div', {
        className: 'max-w-md mx-auto p-6 bg-red-50 rounded-lg shadow-lg'
      }, [
        React.createElement('h2', {
            key: 'title',
          className: 'text-xl font-bold text-red-800 mb-4'
        }, '‚ùå Execution Failed'),
          React.createElement('p', {
            key: 'message',
          className: 'text-red-600'
        }, error instanceof Error ? error.message : 'Unknown error')
      ])
    }
  }





  if (isLoading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="flex items-center space-x-2">
          <div className="w-4 h-4 border-2 border-blue-600 border-t-transparent rounded-full animate-spin"></div>
          <span className="text-gray-600">Generating...</span>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="text-center py-8">
        <p className="text-red-600 text-sm">{error}</p>
          <button
            onClick={renderGeneratedApp}
          className="mt-2 px-3 py-1 bg-blue-600 text-white text-xs rounded hover:bg-blue-700 transition-colors"
          >
          Retry
          </button>
        </div>
    )
  }

  if (!renderedApp) {
    return (
      <div className="text-center py-8">
        <p className="text-gray-600">App ready to display</p>
        </div>
    )
  }

  // Render the generated app component
  return (
    <div className="w-full">
      <Suspense fallback={
        <div className="flex items-center justify-center py-8">
          <div className="w-4 h-4 border-2 border-blue-600 border-t-transparent rounded-full animate-spin"></div>
        </div>
      }>
              {React.createElement(renderedApp)}
      </Suspense>
    </div>
  )
}

export default AppRenderer
